.home .home_wrap{
    max-width: 1200px;
    display: flex;
    margin: 0 auto;
}
.home .home_wrap .item_box{
    background-color: #eee;
    height: 350px;
    margin: 10px;
    width: 25%;
    text-align: center;
}
.home .home_wrap .item_box .img{
    width: 100%;
    height: 250px;
    overflow: hidden;
}
.home .home_wrap .item_box .img img{
    width: 80%;
}
.home .home_wrap .item_box .cart_btn{
    width: 50px;
    height: 20px;
    background-color: #6667ab;
    color: #fff;
    text-align: center;
    display: inline-block;
    margin-top: 20px;
    border-radius: 5px;
}
.home .home_wrap .item_box .cart.add{
    background-color: salmon;
}